import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Icon from '../basics/Icon';
import Link from '../basics/Link';
import Input from '../forms/Input';
import { color, typography } from '../shared/styles';

// prettier-ignore
const Title = styled.div`
  font-weight: ${typography.weight.extrabold};
`;

const DocsLink = styled(Link)`
  float: right;
  svg {
    vertical-align: middle;
  }
`;

// took this from WidgetScreen
const CodeInput = styled(Input)`
  width: 100%;
  margin: 0.5rem 0 0;
`;

// prettier-ignore
const Message = styled.div`
  color: ${color.darker};
  line-height: 18px;
`;

// prettier-ignore
const MessageWrapper = styled.div`
  box-sizing: border-box;
  padding: 15px;
  width: 200px;
`;

function TooltipIgnore({ selector }) {
  return (
    <MessageWrapper>
      <Message>
        <Title>
          Ignored selector
          <DocsLink icon tertiary href="http://docs.chromaticqa.com/configuration" target="_blank">
            <Icon icon="question" />
          </DocsLink>
        </Title>

        <CodeInput value={selector} appearance="code" readOnly />
      </Message>
    </MessageWrapper>
  );
}

TooltipIgnore.propTypes = {
  selector: PropTypes.string.isRequired,
};

export default TooltipIgnore;



