import React from 'react';
import PropTypes from 'prop-types';

import WithTooltip from '../tooltip/WithTooltip';
import TooltipIgnore from './TooltipIgnore';

export default function IgnoredRegions({ imageSize: { width, height }, ignoredRegions, ...props }) {
  const rects = ignoredRegions
    .map(({ selector, regions }) =>
      regions.map(({ top, left, bottom, right }) => (
        <WithTooltip
          svg
          key={`${selector}-${top}-${left}-${bottom}-${right}`}
          placement="top"
          mode="click"
          tooltip={<TooltipIgnore selector={selector} />}
        >
          <rect
            x={left}
            y={top}
            width={right - left}
            height={bottom - top}
            fill="#FFFF00"
            fillOpacity="0.4"
          />
        </WithTooltip>
      ))
    )
    .reduce((a, b) => [...a, ...b], []);

  return (
    <svg viewBox={`0 0 ${width} ${height}`} width={width} height={height} {...props}>
      {rects}
    </svg>
  );
}

IgnoredRegions.propTypes = {
  imageSize: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
  }).isRequired,
  ignoredRegions: PropTypes.arrayOf(
    PropTypes.shape({
      selector: PropTypes.string.isRequired,
      regions: PropTypes.arrayOf(
        PropTypes.shape({
          top: PropTypes.number.isRequired,
          left: PropTypes.number.isRequired,
          bottom: PropTypes.number.isRequired,
          right: PropTypes.number.isRequired,
        }).isRequired
      ),
    })
  ).isRequired,
};



