import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import Link from './../basics/Link';
import Icon from './../basics/Icon';
import WithTooltip from './../tooltip/WithTooltip';
import { color, typography } from './../shared/styles';
import { inlineGlow } from './../shared/animation';

// prettier-ignore
const Title = styled.h1`
  && {
    font-weight: ${typography.weight.extrabold};
    font-size: ${typography.size.m1}px;
    line-height: 1;
    padding: 4px 28px 4px 0;
    overflow-wrap: break-word;
    hyphens: auto;
  }
`;

const DetailStyle = css`
  font-weight: ${typography.weight.bold};
  font-size: ${typography.size.s2 - 1}px;
  line-height: 1;
  line-height: 18px;
  color: ${color.dark};
`;

const Detail = styled.span`${DetailStyle};`;

// prettier-ignore
const DetailLink = styled(Link)`
  ${DetailStyle}

  svg {
    margin-right: 0;
    margin-left: .25em;
  }
`;

const DetailTooltip = styled(WithTooltip)`
  display: inline-block;
  vertical-align: top;
`;

const DetailSeparator = styled.span`
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  margin-right: 14px;
  height: 18px;
  border-right: 1px solid ${color.mediumlight};
`;

const DetailWrapper = styled.div`
  font-size: 0;
  margin-top: 2px;
`;

const Action = styled.div``;

// prettier-ignore
const Meta = styled.div`
  font-size: 0;

  ${Action} {
    position: absolute;
    right: 0;
    top: 0;
  }

  ${props => props.loading  && css`
    ${Title} span, ${Detail} {
      ${inlineGlow}
    }
  `}
`;

// prettier-ignore
const Break = styled.div`
  border-top: 1px solid ${color.mediumlight};
  margin: .75rem 0 .75rem;
`;

// prettier-ignore
const Header = styled.div`
  position: relative;
`;

export default function ComponentHeader({
  loading,
  build,
  component: { name },
  branchTooltip,
  buildTooltip,
  actionButton,
  ...props
}) {
  let detail;

  if (loading) {
    detail = <Detail>Loading</Detail>;
  }

  if (!loading && build) {
    const { number, branch } = build;

    let buildInner = <Detail>Build {number}</Detail>;

    if (buildTooltip) {
      buildInner = (
        <DetailTooltip placement="bottom" mode="click" tooltip={buildTooltip}>
          <DetailLink tertiary>
            {buildInner}
            <Icon icon="arrowdown" />
          </DetailLink>
        </DetailTooltip>
      );
    }

    let branchInner = <Detail>{branch}</Detail>;

    if (branchTooltip) {
      branchInner = (
        <DetailTooltip placement="bottom" mode="click" tooltip={branchTooltip}>
          <DetailLink tertiary>
            {branchInner}
            <Icon icon="arrowdown" />
          </DetailLink>
        </DetailTooltip>
      );
    }

    detail = (
      <DetailWrapper>
        {branchInner}
        <DetailSeparator />
        {buildInner}
      </DetailWrapper>
    );
  }

  return (
    <Header loading={loading} {...props}>
      <Meta loading={loading}>
        <Title>
          <span>{name}</span>
        </Title>
        {detail}

        {!loading && <Action>{actionButton}</Action>}
      </Meta>
      <Break />
    </Header>
  );
}

ComponentHeader.propTypes = {
  loading: PropTypes.bool,
  build: PropTypes.shape({
    id: PropTypes.string.isRequired,
    number: PropTypes.number.isRequired,
    branch: PropTypes.string.isRequired,
  }),
  component: PropTypes.shape({
    name: PropTypes.string,
  }),
  buildTooltip: PropTypes.node,
  branchTooltip: PropTypes.node,
  actionButton: PropTypes.node,
};

ComponentHeader.defaultProps = {
  loading: false,
  build: null,
  component: {
    name: 'loading',
    specs: [],
    builds: [],
  },
  buildTooltip: false,
  branchTooltip: false,
  actionButton: null,
};



