import ExtendableError from 'es6-error';

// TODO -- expand to UnserializablePropError, more helpful
//   https://github.com/hichroma/chromatic/issues/158
export default class UnserializeableInputError extends ExtendableError {
  constructor(data) {
    super();
    this.data = data;
  }
  toString() {
    try {
      return `found node with unserializable input ${this.data}`;
    } catch (e) {
      // If we cannot even call `.toString()` on `this.data`
      return `found node with unserializable input`;
    }
  }
}



