import React from 'react';
import PropTypes from 'prop-types';
import { compose, withState, withHandlers } from 'recompose';
import styled from 'styled-components';

import { styles } from '../../../../../lib/components';
import ScannerButtons from './ScannerButtons';

const { color } = styles;

const Stack = styled.div`
  margin-bottom: 4px;
  position: relative;
`;

const ScannerButtonsWrapper = styled.div`
  /* Needed for position absolute expand button */
  position: relative;
`;

const ScannerRow = styled(ScannerButtons)`
  display: inline-block;
  vertical-align: top;
  margin: 4px 0;
`;

const ScannerButtonPath = styled.div`
  width: 8px;
  height: 16px;
  border-left: 1px solid ${color.medium};
  border-bottom: 1px solid ${color.medium};
  margin-left: 6px;
  margin-right: 4px;
  display: inline-block;
  vertical-align: top;
`;

function ScannerButtonStack({
  specs,
  onCreateSpec,
  onShowComponentSpecs,
  expanded,
  onSetExpanded,
}) {
  const visibleSpecs = expanded ? specs.slice().reverse() : specs.slice(0, 1);
  return (
    <Stack>
      {visibleSpecs.map((spec, index) => {
        let onExpandOrRetract = {};
        if (specs.length > 1) {
          if (!expanded) {
            onExpandOrRetract = { onExpand: () => onSetExpanded(true) };
          } else if (index === specs.length - 1) {
            onExpandOrRetract = { onRetract: () => onSetExpanded(false) };
          }
        }

        return (
          <ScannerButtonsWrapper key={spec.key}>
            {index !== 0 && <ScannerButtonPath style={{ marginLeft: `${index * 20}px` }} />}
            <ScannerRow
              spec={spec}
              {...onExpandOrRetract}
              onCreateSpec={() => onCreateSpec(spec)}
              onShowComponentSpecs={onShowComponentSpecs}
            />
          </ScannerButtonsWrapper>
        );
      })}
    </Stack>
  );
}

ScannerButtonStack.propTypes = {
  specs: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string.isRequired,
    })
  ).isRequired,
  onCreateSpec: PropTypes.func.isRequired,
  onShowComponentSpecs: PropTypes.func.isRequired,
  expanded: PropTypes.bool.isRequired,
  onSetExpanded: PropTypes.func.isRequired,
};

export default compose(
  withState('expanded', 'onSetExpandedInner', false),
  withHandlers({
    onSetExpanded: ({ onSetExpandedInner, onResize }) => expanded => {
      onSetExpandedInner(expanded);
      onResize();
    },
  })
)(ScannerButtonStack);



